import Omic from './Omic'

import OmicUitls from './utils/OmicUitls'

export default class Omicx extends Omic {
  constructor(data) {
    data = Object.assign({
      $__tag_closed_: false, //
      $__tag_self_: Omicx //
    }, data)
    super(data)
  }
  beforeRender() {
    //,super.beforeRender()
    OmicUitls.injectEndTag(this) // div
  }
}


