import Omicx from '../Omicx'

export default class OmicContainer extends Omicx {
  constructor(data) {
    data = Object.assign({
      $__tag_closed_: false, //
      $__tag_self_: OmicContainer, //
      fluid: 'false'
    }, data)
    super(data)
  }
  render() {
    return `<div ref='OmicContainer-div-{{key}}' class='mdui-container{{if "true"===fluid}}-fluid{{/if}} {{clazz}}' style='{{style}}'>`
  }
}


