import Omi from 'omi'

import '../3rd/mdui@v0.1.2/css/mdui.css'
import '../3rd/mdui@v0.1.2/js/mdui.js'
import '../css/index.less'

import OmicUitls from './utils/OmicUitls'

export default class Omic extends Omi.Component {
  constructor(data) {
    //
    let key = Math.round(10000000000000000 * Math.random()) //
    data = Object.assign({
      $__tag_closed_: true, //
      $__tag_self_: Omic, //
      key: key,
      id: 'Omic-' + key,
      name: 'Omic-' + key,
      clazz: '',
      style: '',
      disabled: ''
    }, data);
    super(data)
    OmicUitls.copyEvent(this) //datacopy
  }
  install() {} //
  installed() {} //HTML
  uninstall() {} //remove
  beforeUpdate() {} //
  afterUpdate() {} //
  style() {} //
  beforeRender() {} //
  render() {} //
  $makeHTML(name, ctor) {
    Omi.makeHTML(name, ctor) //
  }
  $get(omiId) {
    return Omi.get(omiId) //omi-id
  }
}


