import Omicx from '../Omicx'

export default class OmicCol extends Omicx {
  constructor(data) {
    data = Object.assign({
      $__tag_closed_: false, //
      $__tag_self_: OmicCol, //
    }, data)
    super(data)
  }
  render() {
    return `<div ref='OmicCol-div-{{key}}'
      class='mdui-col {{clazz}}'
      style='{{style}}'
    >`
  }
}


